html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

li {
  list-style: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.header {
  background-color: #000000;
  padding: 0 3rem;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  gap: 1rem;
}

.header__title {
  color: #FFFFFF;
  font-weight: 800;
}

.list {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  gap: 1rem;
}

.list__item a {
  color: #FFFFFF;
}

.cover {
  position: relative;
  width: 100%;
  height: 100vh;
  overflow: hidden;
}

.cover__content {
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  gap: 2rem;
  padding-top: 7rem;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0.9), rgba(0, 0, 0, 0));
}

.cover__content__title {
  font-weight: 900;
  font-size: 4.8rem;
  color: #FFFFFF;
  text-transform: uppercase;
  margin: 0;
}

.cover__content__paragraph {
  font-weight: 700;
  font-size: 2.4rem;
  width: 100rem;
  color: #FFFFFF;
  text-align: center;
}

.cover__img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: top;
}

.btn {
  width: 27rem;
  padding: 0.8rem 3rem;
  border-radius: 4px;
  cursor: pointer;
  background: transparent;
  text-transform: uppercase;
  transition: all 0.5s;
}
.btn:hover {
  background-color: #000000;
  color: #FFFFFF;
}

.new-content {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
  gap: 3rem;
}

.news__title {
  font-weight: 700;
  color: #ff1b1b;
}

.card {
  max-width: 40rem;
  border-radius: 4px;
  overflow: hidden;
  padding: 2rem;
  box-shadow: 10px 9px 27px -6px rgba(0, 0, 0, 0.39);
  -webkit-box-shadow: 10px 9px 27px -6px rgba(0, 0, 0, 0.39);
  -moz-box-shadow: 10px 9px 27px -6px rgba(0, 0, 0, 0.39);
  transition: 0.4s;
}
.card:hover {
  background: #ff1b1b;
  transform: translateY(-25px);
}
.card:hover .card-img {
  width: 110%;
}
.card:hover .card__content__title {
  color: #FFFFFF;
}
.card:hover .card__content__paragraph {
  color: #FFFFFF;
}

.card__content-img {
  width: 100%;
  border-radius: 4px;
  overflow: hidden;
}

.card-img {
  width: 100%;
  object-fit: cover;
  object-position: center;
  transition: 0.4s;
}

.card__content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 1rem;
}

.card__content__title {
  font-weight: 400;
  margin: 0;
}

.card__content__paragraph {
  text-align: justify;
}

.modelos {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  gap: 0;
  background-color: #000000;
}

.modelos__title {
  color: #FFFFFF;
  font-weight: 900;
}

.modelo {
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 0;
}

.modelo__img {
  width: 50%;
}

.modelo__img img {
  width: 100%;
  height: 70rem;
  object-fit: cover;
  object-position: center;
}

.modelo-description {
  width: 50%;
  padding: 4rem;
}
.modelo-description .btn-link {
  background: transparent;
}
.modelo-description .btn-link .btn {
  border: 1px solid #FFFFFF;
  color: #FFFFFF;
  transition: all 0.3s;
}
.modelo-description .btn-link .btn:hover {
  background-color: #FFFFFF;
  color: #000000;
}

.modelo-description__title {
  color: #FFFFFF;
  font-weight: 700;
  text-align: left;
}

.modelo-description__paragraph {
  color: #FFFFFF;
  font-weight: 400;
  max-width: 50rem;
  text-align: justify;
}

.footer {
  background-color: #000000;
  z-index: 100;
}
.footer ul {
  justify-content: space-evenly;
}
.footer a {
  color: #FFFFFF;
}

.copyrigth {
  color: #FFFFFF;
  text-align: center;
}